import React, { useEffect, useState } from 'react'
import { Button, Card, Col, Table } from 'react-bootstrap';
import { Row } from 'react-bootstrap';
import callApi from '../../../helpers/conectorApi';
import Aux from '../../../hoc/_Aux';
import { alert_exitoso, alert_warning } from '../../../helpers/Notificacion';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';
import { SubCategoriaGastoUpSert } from './SubCategoriaGastoUpSert';
export const SubCategoriaGastoListar = ({ itemCategoria, exit }) => {
  const [loading, setLoading] = useState(true)
  const [listSubCategorias, setListSubCategorias] = useState([]);
  const [abrirModal, setAbrirModal] = useState(false);
  const initData = {
    sub_categoriaId: '',
    categoriaId: itemCategoria.categoriaId,
    nombre: '',
    estadoId: 1
  };
  const [dataInicial, setdataInicial] = useState(initData);

  const handleOpenModal = () => {
    setAbrirModal(true);
    setdataInicial(initData);
  }
  const handleEditar = (id) => {
    const { sub_categoriaId, categoriaId, nombre, estadoId } = listSubCategorias.find(item => item.sub_categoriaId === id);
    setdataInicial({
      sub_categoriaId,
      categoriaId,
      nombre,
      estadoId
    });
    setAbrirModal(true);
  }
  const handleDelete = (id) => {
    const MySwal = withReactContent(Swal);
    MySwal.fire({
      title: 'Alerta?',
      text: 'Esta seguro que desea eliminar el elemento',
      type: 'warning',
      showCloseButton: true,
      showCancelButton: true
    }).then(async (willDelete) => {
      if (willDelete.value) {
        let method = 'DELETE';
        let response = await callApi(`ctl/subcategoria/${id}`, {
          method
        });
        if (response) {
          alert_exitoso(response);
          let listActual = listSubCategorias.filter(item => item.sub_categoriaId !== id);
          setListSubCategorias(listActual);
        }
      } else {
        alert_warning('No se eliminó ningún elemento');
      }
    });
  }


  const GetSubCategorias = async () => {
    setLoading(true);
    let response = await callApi(`ctl/subcategoria?categoriaId=${itemCategoria.categoriaId}&estadoId=1;2`);
    if (response) {
      setListSubCategorias(response);
    }
    setLoading(false);
  }
  useEffect(() => {
    GetSubCategorias();
  }, [itemCategoria]);

  return (
    <Aux>
      <Row>
        <Col xl={12} md={12}>
          <Button
            key="btnBack"
            variant="success"
            size="sm"
            onClick={(e) => exit()}>
            <i className="feather icon-arrow-left" />
            Categorias
          </Button>
          <Card>
            <Card.Header>
              <h5>[{itemCategoria.nombre}] - Sub Categorias</h5>
              <Button variant="success" className="btn-sm btn-round has-ripple" onClick={handleOpenModal}><i className="feather icon-plus" />Add</Button>
            </Card.Header>
            <Card.Body>
              <Table responsive hover borderless>
                <thead>
                  <tr>
                    <th>No.</th>
                    <th>Nombre</th>
                    <th style={{ textAlign: "center" }}>Estado</th>
                    <th style={{ textAlign: "center" }}>Opciones</th>
                  </tr>
                </thead>
                <tbody>
                  {
                    listSubCategorias && listSubCategorias.map((item, index) => {
                      return (
                        <tr key={`sub_categoriaId${item.sub_categoriaId}`}>
                          <td>{index + 1}</td>
                          <td>{item?.nombre}</td>
                          <td style={{ textAlign: "center" }}>
                            {
                              item.estadoId === 1 ?
                                <label className="badge badge-light-success">{item?.Estado?.descripcion}</label>
                                :
                                <label className="badge badge-light-danger">{item?.Estado?.descripcion}</label>
                            }
                          </td>
                          <td style={{ textAlign: "center" }}>
                            <a href={"#"} onClick={() => { handleEditar(item.sub_categoriaId) }}><i className="icon feather icon-edit f-w-600 f-16 m-r-15 text-c-green" /></a>
                            <a href={"#"} onClick={() => { handleDelete(item.sub_categoriaId) }}><i className="feather icon-trash-2 f-w-600 f-16 text-c-red" /></a>
                          </td>
                        </tr>
                      )
                    })
                  }
                </tbody>
              </Table>
            </Card.Body>
            {
              abrirModal === true &&
              <SubCategoriaGastoUpSert abrirModal={abrirModal} setAbrirModal={setAbrirModal} GetSubCategorias={GetSubCategorias} dataInicial={dataInicial} />
            }
          </Card>
        </Col>
      </Row>
    </Aux>
  )
}
