import React, { useEffect, useState } from 'react'
import { Button, Card, Col, Table } from 'react-bootstrap';
import { Row } from 'react-bootstrap';
import callApi from '../../../helpers/conectorApi';
import Aux from '../../../hoc/_Aux';
import { alert_exitoso, alert_warning } from '../../../helpers/Notificacion';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';
import { CategoriaGastoUpSert } from './CategoriaGastoUpSert';
import { SubCategoriaGastoListar } from './SubCategoriaGastoListar';
import { PresupuestoUpSert } from './PresupuestoUpSert';
export const PresupuestoListar = () => {
  const [loading, setLoading] = useState(true);
  const [upSert, setUpSert] = useState(false);
  const [listPresupuesto, setListPrespuesto] = useState([]);
  const [listMeses, setListMeses] = useState([]);
  const [listCategorias,setListCategorias]=useState([]);
  const [abrirModal, setAbrirModal] = useState(false);
  const [subCategoriaComponent, setSubCategoriaComponent] = useState(false);
  const [itempresupuestoIdShow, setItempresupuestoIdShow] = useState(0);
  const initData = {
    presupuestoId: '',
    anio: '',
    mesId:'',
    descripcion:'',
    estadoId: 1,
    detalle:[]
  };
  console.log({listPresupuesto});
  const [dataInicial, setdataInicial] = useState(initData);

  const handleCloseUpSert=()=>{
    setUpSert(false);
  }
  const handleOpenModal = () => {
    setAbrirModal(true);
    setdataInicial(initData);
  }
  const handleEditar = (id) => {
    const { presupuestoId,anio,descripcion, mesId, estadoId,Detalle } = listPresupuesto.find(item => item.presupuestoId === id);
    setdataInicial({
      presupuestoId,
      anio,
      descripcion,
      mesId,
      estadoId,
      Detalle
    });
    setUpSert(true);
  }
  const handleDelete = (id) => {
    const MySwal = withReactContent(Swal);
    MySwal.fire({
      title: 'Alerta?',
      text: 'Esta seguro que desea eliminar el elemento',
      type: 'warning',
      showCloseButton: true,
      showCancelButton: true
    }).then(async (willDelete) => {
      if (willDelete.value) {
        let method = 'DELETE';
        let response = await callApi(`ctl/categoria/${id}`, {
          method
        });
        if (response) {
          alert_exitoso(response);
          let listActual = listPresupuesto.filter(item => item.presupuestoId !== id);
          setListPrespuesto(listActual);
        }
      } else {
        alert_warning('No se eliminó ningún elemento');
      }
    });
  }

   const GetPresupuesto = async () => {
    setLoading(true);
    let response = await callApi('ctl/presupuesto?estadoId=1;2');
    if (response) {
      setListPrespuesto(response);
    }
    setLoading(false);
  }
  const GetMeses = async () => {
    setLoading(true);
    let response = await callApi('ctl/catalogo/mes');
    if (response) {
      setListMeses(response);
    }
    setLoading(false);
  }
  const GetCategorias = async () => {
    setLoading(true);
    let response = await callApi('ctl/categoria?estadoId=1&include=0');
    if (response) {
      setListCategorias(response);
    }
    setLoading(false);
  }
  const handleNew=()=>{
    setdataInicial(initData);
    setUpSert(true);
  }

  useEffect(() => {
    GetPresupuesto();
    GetMeses();
    GetCategorias();
  }, []);

  return (
    <>
      {
        !upSert ?
          <Aux>
            <Row>
              <Col xl={12} md={6}>
                <Card>
                  <Card.Header>
                    <h5>Presupuesto</h5>
                    <Button variant="success" className="btn-sm btn-round has-ripple" onClick={()=>{handleNew()}}><i className="feather icon-plus" />Add</Button>
                  </Card.Header>
                  <Card.Body>
                    <Table responsive hover borderless>
                      <thead>
                        <tr>
                          <th>No.</th>
                          <th>Descripción</th>
                          <th style={{ textAlign: "center" }}>Año</th>
                          <th style={{ textAlign: "center" }}>Mes</th>
                          <th style={{ textAlign: "center" }}>Estado</th>
                          <th style={{ textAlign: "center" }}>Opciones</th>
                        </tr>
                      </thead>
                      <tbody>
                        {
                          listPresupuesto && listPresupuesto.map((item, index) => {
                            return (
                              <tr key={`presupuestoId${item.presupuestoId}`}>
                                <td>{index + 1}</td>
                                <td>{item?.descripcion}</td>
                                <td style={{ textAlign: "center" }}>{item.anio}</td>
                                <td style={{ textAlign: "center" }}>{item?.Mes?.descripcion}</td>
                                <td style={{ textAlign: "center" }}>
                                  {
                                    item.estadoId === 1 ?
                                      <label className="badge badge-light-success">{item?.Estado?.descripcion}</label>
                                      :
                                      <label className="badge badge-light-danger">{item?.Estado?.descripcion}</label>
                                  }
                                </td>
                                <td style={{ textAlign: "center" }}>
                                  <a href={"#"} onClick={() => { handleEditar(item.presupuestoId) }}><i className="icon feather icon-edit f-w-600 f-16 m-r-15 text-c-green" /></a>
                                  <a href={"#"} onClick={() => { handleDelete(item.presupuestoId) }}><i className="feather icon-trash-2 f-w-600 f-16 text-c-red" /></a>
                                </td>
                              </tr>
                            )
                          })
                        }
                      </tbody>
                    </Table>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Aux>
          : <PresupuestoUpSert
          setUpSert={setUpSert}
            dataInicial={dataInicial} 
            handleCloseUpSert={handleCloseUpSert}
            GetPresupuesto={GetPresupuesto}
            listMeses={listMeses}
            listCategorias={listCategorias}
            />
      }
    </>
  )
}
