import React, { useState, useEffect } from 'react'
import { Col, Form, Modal } from 'react-bootstrap';
import { ValidationForm, TextInput, SelectGroup } from 'react-bootstrap4-form-validation';
import callApi from '../../../helpers/conectorApi';
import { alert_exitoso, alert_warning } from '../../../helpers/Notificacion';
import { useForm } from '../../hooks/useForm';
import Loading from './Loading';
import { v4 as uuidv4 } from 'uuid';
export const PresupuestoDetalleUpSert = ({ dataInicial, abrirModal, setAbrirModal, handleAddItem, handleEditItem, listCategorias, listItemsDetalle }) => {
    const [values, handleOnChange] = useForm(dataInicial);
    const [loading, setLoading] = useState(false);
    const [listCategoriasDisponibles, setListCategoriasDisponibles] = useState([]);

    useEffect(() => {
        let listDisponibles = [];
        listCategorias.map((item) => {
            let existe = listItemsDetalle.find(i => Number(i.categoriaId) === Number(item.categoriaId) && Number(i.categoriaId)!==Number(dataInicial.categoriaId) && Number(i.estadoId)!==3);
            if (!existe) {
                listDisponibles.push(item);
            }
        });
        setListCategoriasDisponibles(listDisponibles);
    }, []);


    const handleOnSubmit = (e) => {
        e.preventDefault();
        if (Number(values.monto) <= 0) {
            alert_warning("El monto debe ser mayor a 0");
            return false;
        }
        let itemCategoria = listCategorias.find(i => Number(i.categoriaId) === Number(values.categoriaId));
        let { nombre } = itemCategoria;
        if (dataInicial.presupuesto_detalleId > 0 || String(dataInicial.id).trim().length > 0) {
            let datos = values;
            datos.nombre = nombre;
            handleEditItem(values);
        } else {
            let datos = values;
            datos.nombre = nombre;
            datos.id = uuidv4();
            handleAddItem(datos);
            console.log(datos);
        }
        setAbrirModal(false);
    }
    const handleErrorSubmit = (e, formData, errorInputs) => {
        alert_warning("Por favor complete toda la información solicitada por el formulario");
    };

    const errorMessage = "Campo obligatorio";
    const textTransform = 'capitalize';
    return (
        <Modal show={abrirModal} onHide={() => setAbrirModal(false)}>
            {
                loading === true ?
                    <Loading />
                    : <>
                        <Modal.Header closeButton>
                            <Modal.Title as="h5">{dataInicial.presupuesto_detalleId > 0 ? 'Actualizar' : 'Nuevo'}</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <ValidationForm onSubmit={handleOnSubmit} onErrorSubmit={handleErrorSubmit}>
                                <Form.Row>
                                    <Form.Group as={Col} md="12">
                                        <Form.Label htmlFor="categoriaId">Categoría</Form.Label>
                                        <SelectGroup
                                            name="categoriaId"
                                            id="categoriaId"
                                            value={values.categoriaId}
                                            required
                                            errorMessage={errorMessage}
                                            onChange={handleOnChange}>
                                            <option value="">--Seleccione--</option>
                                            {
                                                listCategoriasDisponibles && listCategoriasDisponibles.map(({ categoriaId, nombre }) => {
                                                    return (
                                                        <option value={categoriaId} key={`categoriaId${categoriaId}`}>{nombre}</option>
                                                    )
                                                })
                                            }
                                        </SelectGroup>
                                    </Form.Group>
                                    <Form.Group as={Col} md="12">
                                        <Form.Label htmlFor="monto">Monto</Form.Label>
                                        <TextInput
                                            name="monto"
                                            id="monto"
                                            required
                                            value={values.monto}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Monto"
                                            autoComplete="off"
                                            style={{ textTransform: textTransform }}
                                            type="number"
                                        />
                                    </Form.Group>
                                    {
                                        dataInicial.presupuesto_detalleId > 0 &&
                                        <Form.Group as={Col} md="12">
                                            <Form.Label htmlFor="estadoId">Estado</Form.Label>
                                            <SelectGroup
                                                name="estadoId"
                                                id="estadoId"
                                                value={values.estadoId}
                                                required
                                                errorMessage={errorMessage}
                                                onChange={handleOnChange}>
                                                <option value="">Seleccione un estado</option>
                                                <option value="1">Activo</option>
                                                <option value="2">Inactivo</option>
                                            </SelectGroup>
                                        </Form.Group>
                                    }
                                </Form.Row>
                                <Form.Row className="align-items-center m-l-0">
                                    <Col />
                                    <Col />
                                    <Col className="text-right">
                                        <button type="button" onClick={() => { setAbrirModal(false) }} className="btn btn-warning"> Cancelar</button>
                                    </Col>
                                    <Col className="text-right">
                                        <button type="submit" className="btn btn-success"> {dataInicial.presupuesto_detalleId > 0 ? 'Actualizar' : 'Registrar'}</button>
                                    </Col>
                                </Form.Row>

                            </ValidationForm>
                        </Modal.Body>
                    </>
            }
        </Modal>
    )
}
