import React, { useState } from 'react'
import { useEffect } from 'react';
import { Button, Card, Col, Form, Modal, Row, Table } from 'react-bootstrap';
import { ValidationForm, TextInput, SelectGroup } from 'react-bootstrap4-form-validation';
import callApi from '../../../helpers/conectorApi';
import { alert_exitoso, alert_warning } from '../../../helpers/Notificacion';
import Aux from '../../../hoc/_Aux';
import { useForm } from '../../hooks/useForm';
import Loading from './Loading';
import { PresupuestoDetalleUpSert } from './PresupuestoDetalleUpSert';
const initData = {
    id: '',
    presupuesto_detalleId: 0,
    categoriaId: 0,
    monto: 0,
    estadoId: 1
};
export const PresupuestoUpSert = ({ dataInicial, GetPresupuesto, handleCloseUpSert, listMeses, listCategorias,setUpSert }) => {
    const [values, handleOnChange] = useForm(dataInicial);
    const [loading, setLoading] = useState(false);
    const [listAnios, setListAnios] = useState([]);
    const [listMesesMostrar, setListMesesMostrar] = useState([]);
    const [listItemsDetalle, setListItemsDetalle] = useState([]);
    const [dataInicialItem, setDataInicialItem] = useState(initData);
    const [abrirModal, setAbrirModal] = useState(false);



    const handleAddItem = (item) => {
        console.log({'tipo':'Edit',item});
        let itemsActuales=listItemsDetalle;
        itemsActuales.push(item);
        setListItemsDetalle(itemsActuales);
    }

    const handleDeleteItem = (item) => {
        let nuevosItems = listItemsDetalle.filter(i => Number(i.presupuesto_detalleId) > 0 && Number(i.presupuesto_detalleId) !== Number(item.presupuesto_detalleId) || i.id !== item.id);
        if (Number(item.presupuesto_detalleId) > 0) {
            item.estadoId = 3;
            nuevosItems.push(item);
        }
        setListItemsDetalle(nuevosItems);
    }
    const handleEditItem = (item) => {
        let listItemsActuales = listItemsDetalle;
        console.log({'tipo':'Edit',item,listItemsActuales});
        let nuevosItems = listItemsActuales.map(fila => {
            console.log({itemMap:fila});
            if ((Number(fila.presupuesto_detalleId) === Number(item.presupuesto_detalleId) && Number(fila.presupuesto_detalleId)>0) || (String(fila.id) === String(item.id) && Number(item.presupuesto_detalleId)===0)) {
                console.log("Entro");
                return item;
            } else {
                return fila;
            }
        });
        setListItemsDetalle(nuevosItems);
    }


    const NuevoRegistro = async () => {
        let datos=values;
        datos.detalle=listItemsDetalle;
        let response = await callApi('ctl/presupuesto', {
            method: 'POST',
            body: JSON.stringify(datos)
        });

        if (response) {
            alert_exitoso("Presupuesto agregado exitosamente");
            GetPresupuesto();
            setUpSert(false);
        }
    }
    const ActualizarRegistro = async () => {
        let datos=values;
        datos.detalle=listItemsDetalle;
        let response = await callApi('ctl/presupuesto', {
            method: 'PUT',
            body: JSON.stringify(datos)
        });

        if (response) {
            alert_exitoso(response);
            GetPresupuesto();
            setUpSert(false);
        }
    }
    const handleOnSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);
        if (dataInicial.presupuestoId > 0) {
            await ActualizarRegistro();
        } else {
            await NuevoRegistro();
        }
       
        setLoading(false);
    }
    const handleErrorSubmit = (e, formData, errorInputs) => {
        alert_warning("Por favor complete toda la información solicitada por el formulario");
    };

    const handleOpenModal=()=>{
        setAbrirModal(true);
        setDataInicialItem(initData);
    }

    const handleEditar = (idDb, idClient) => {
        if (Number(idDb) > 0) {
            let datos = listItemsDetalle.find(i => Number(i.presupuesto_detalleId) === Number(idDb));
            setDataInicialItem(datos);
        } else {
            let datos = listItemsDetalle.find(i => i.id === idClient);
            setDataInicialItem(datos);
        }
        setAbrirModal(true);
    }

    useEffect(() => {
        if (dataInicial?.Detalle) {
            let items = dataInicial.Detalle.map(item => {
                let { presupuesto_detalleId, categoriaId, monto, estadoId, Categoria: { nombre } } = item;
                return {
                    presupuesto_detalleId,
                    categoriaId,
                    monto,
                    estadoId,
                    nombre,
                    id:''
                }
            });
            setListItemsDetalle(items);
        }
    }, []);

    useEffect(() => {
        let anios = [];
        let anioActual = new Date().getFullYear();
        let mesActual = new Date().getMonth();
        anios.push({
            anio: anioActual
        });
        if (Number(mesActual) > 10) {
            anios.push({
                anio: Number(anioActual) + 1
            });
        }
        let meses = listMeses.filter(i => Number(i.mesId) > Number(mesActual));
        setListAnios(anios);
        setListMesesMostrar(meses);
    }, [])

    const errorMessage = "Campo obligatorio";
    const textTransform = 'capitalize';
    return (
        <Aux>
            <Row>
                <Col xl={12} md={6}>
                    <Button
                        key="btnBack"
                        variant="success"
                        size="sm"
                        onClick={(e) => { handleCloseUpSert() }}>
                        <i className="feather icon-arrow-left" />
                        Lista de Presupuesto
                    </Button>
                    <Card>
                        <Card.Header>
                            <h5>{dataInicial.presupuestoId > 0 ? 'Actualizar Presupuesto' : 'Nuevo Presupuesto'}</h5>
                        </Card.Header>
                        <Card.Body>
                            <ValidationForm onSubmit={handleOnSubmit} onErrorSubmit={handleErrorSubmit}>
                                <Form.Row>

                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="anio">Año</Form.Label>
                                        <SelectGroup
                                            name="anio"
                                            id="anio"
                                            value={values.anio}
                                            required
                                            errorMessage={errorMessage}
                                            onChange={handleOnChange}>
                                            <option value="">--Seleccione--</option>
                                            {
                                                listAnios && listAnios.map(({ anio }) => {
                                                    return (
                                                        <option value={anio} key={`anio${anio}`}>{anio}</option>
                                                    )
                                                })
                                            }

                                        </SelectGroup>
                                    </Form.Group>
                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="mesId">Mes</Form.Label>
                                        <SelectGroup
                                            name="mesId"
                                            id="mesId"
                                            value={values.mesId}
                                            required
                                            errorMessage={errorMessage}
                                            onChange={handleOnChange}>
                                            <option value="">--Seleccione--</option>
                                            {
                                                listMesesMostrar && listMesesMostrar.map(({ mesId, descripcion }) => {
                                                    return (
                                                        <option value={mesId} key={`mesId${mesId}`}>{descripcion}</option>
                                                    )
                                                })
                                            }

                                        </SelectGroup>
                                    </Form.Group>
                                    <Form.Group as={Col} md="12">
                                        <Form.Label htmlFor="descripcion">Descripción</Form.Label>
                                        <TextInput
                                            name="descripcion"
                                            id="descripcion"
                                            required
                                            value={values.descripcion}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Descripción"
                                            autoComplete="off"
                                            style={{ textTransform: textTransform }}
                                            type="text"
                                        />
                                    </Form.Group>
                                    {
                                        dataInicial.categoriaId > 0 &&
                                        <Form.Group as={Col} md="12">
                                            <Form.Label htmlFor="estadoId">Estado</Form.Label>
                                            <SelectGroup
                                                name="estadoId"
                                                id="estadoId"
                                                value={values.estadoId}
                                                required
                                                errorMessage={errorMessage}
                                                onChange={handleOnChange}>
                                                <option value="">Seleccione un estado</option>
                                                <option value="1">Activo</option>
                                                <option value="2">Inactivo</option>
                                            </SelectGroup>
                                        </Form.Group>
                                    }
                                </Form.Row>
                                <h6>Detalle</h6>
                                <hr></hr>
                                <Table responsive hover borderless>
                                    <thead>
                                        <tr>
                                            <th>No.</th>
                                            <th>Categoría</th>
                                            <th style={{ textAlign: "center" }}>Monto</th>
                                            <th style={{ textAlign: "center" }}>Estado</th>
                                            <th style={{ textAlign: "center" }}>Opciones</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            listItemsDetalle && listItemsDetalle.map((item, index) => {
                                                let { estadoId } = item;
                                                console.log({render:true,item,estadoId});
                                                if (Number(estadoId) !== 3) {
                                                    return (
                                                        <tr key={`detalleId${index}`}>
                                                            <td>{index + 1}</td>
                                                            <td>{item?.nombre}</td>
                                                            <td style={{ textAlign: "center" }}>
                                                                {item?.monto}
                                                            </td>
                                                            <td style={{ textAlign: "center" }}>
                                                                {
                                                                    Number(item.estadoId) === 1 ?
                                                                        <label className="badge badge-light-success">Activo</label>
                                                                        :
                                                                        <label className="badge badge-light-danger">Inactivo</label>
                                                                }
                                                            </td>
                                                            <td style={{ textAlign: "center" }}>
                                                                <a href={"#"} onClick={() => { handleEditar(item.presupuesto_detalleId, item.id) }}><i className="icon feather icon-edit f-w-600 f-16 m-r-15 text-c-green" /></a>
                                                                <a href={"#"} onClick={() => { handleDeleteItem(item) }}><i className="feather icon-trash-2 f-w-600 f-16 text-c-red" /></a>
                                                            </td>
                                                        </tr>
                                                    )
                                                }
                                            })
                                        }
                                    </tbody>
                                    <tfoot>
                                        <tr>
                                            <td style={{ textAlign: "left" }} colSpan="5">
                                                <a href={"#"} onClick={() => { handleOpenModal() }}><i className="feather icon-add f-w-600 f-16 text-c-red" />Nuevo</a>
                                            </td>
                                        </tr>
                                    </tfoot>
                                </Table>
                                <Form.Row className="align-items-center m-l-0">
                                    <Col xl={9} md={12} />
                                    <Col xl={3} md={12} className="text-right">
                                        <button type="button" onClick={() => { handleCloseUpSert() }} className="btn btn-warning m-r-10"> Cancelar</button>
                                        <button type="submit" className="btn btn-success"> {dataInicial.presupuestoId > 0 ? 'Actualizar' : 'Registrar'}</button>
                                    </Col>
                                </Form.Row>
                            </ValidationForm>
                        </Card.Body>
                        {
                            abrirModal === true &&
                            <PresupuestoDetalleUpSert
                                abrirModal={abrirModal}
                                setAbrirModal={setAbrirModal}
                                dataInicial={dataInicialItem}
                                handleAddItem={handleAddItem}
                                handleEditItem={handleEditItem}
                                listCategorias={listCategorias}
                                listItemsDetalle={listItemsDetalle}
                            />
                        }
                    </Card>
                </Col>
            </Row>
        </Aux>
    )
}
